.qrScanner {
  background-color: antiquewhite;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgba($color: #1a79d3, $alpha: 1.0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  gap: 15px;
}

.reader {
  width: 100%;
  max-width: 500px;

}

.container {
  // max-width: 450px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

.qr {
  width: 100%;
  max-width: 320px;
}

.control {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.headline {
  margin: 0;
}

.select {
  width: 100%;
  max-width: 320px;
  color: rgb(6, 5, 19);
}